import React from 'react';
import bootstrap from 'bootstrap';

export default function About(props) {
  return (
    <>
    <h1 className='my-2' style={{color: props.mode==='dark'?'white':'black'}}>About This Website</h1>
      <div className="accordion my-3" id="accordionExample">
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingOne">
      <button className="accordion-button" type="button" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}} data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
        <strong>Analyze your Text</strong>
      </button>
    </h2>
    <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}}>
        TextUtils will help users to analyze text as it has a word counter and character counter and it also has converters like Uppercase, Lowercase and many more converter.
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingTwo">
      <button className="accordion-button collapsed" type="button" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}} data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
       <strong>Free to use</strong>
      </button>
    </h2>
    <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}}>
        This Website is free to use for everyone.
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header" id="headingThree">
      <button className="accordion-button collapsed" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}} type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
        <strong>Developer</strong>
      </button>
    </h2>
    <div id="collapseThree" className="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
      <div className="accordion-body" style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}}>
        This website is Developed by Anurag
      </div>
    </div>
  </div>
</div>
</>
  )
}
