import React , {useState} from 'react'

export default function TextForm(props) {
  const handleUpClick = function(){
    //console.log("Upper Case was Clicked");
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("Text converted To UpperCase" , "success");
  }
  const handleLpClick = function(){
    //console.log("Lower Case was Clicked");
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("Text converted To LowerCase" , "success");
  }

  const handleClearClick = function(){
    setText("");
    props.showAlert("Text has been Cleared" , "warning");
  }
  const handleCapClick = function(){
    let newText = text.split(" ").map((currentValue) => {
      let newText = currentValue[0].toUpperCase() + currentValue.slice(1);
      return newText;
      
  });
  setText(newText.join(" "));
  props.showAlert("Text Capitalized" , "success");
  }

  const handleInClick = function(){
    let word = text.split(" ");
    word = word.reverse();
    let newText = word.join(" ");
    setText(newText);
    props.showAlert("Text Reversed" , "success");
  }
  const handleCopyClick = function(){
    navigator.clipboard.writeText(text);
    props.showAlert("Text Copied to Clipboard" , "success");
  }

  const handleExtraSp = function(){
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.showAlert("Extra Spaces has been removed" , "success");
  }
  const handleOnChange = function(event){
    //console.log("On Change");
    setText(event.target.value);
  }
  const [text,setText] = useState('');
  //text = "adad" //Wrong way to change the state
  //setText("adad") //correct way to change the state
  return (
      <>
      <div className="container">
<div className="mb-3">
  <h1 className='my-4' style={{color: props.mode==='dark'?'white':'black'}}>{props.heading}</h1>
  <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor: props.mode==='light'?'white':'#00415a', color: props.mode==='dark'?'white':'black'}} id="exampleFormControlTextarea1" rows="8"></textarea>
</div>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleUpClick}>Convert to Upper Case</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleLpClick}>Convert to Lower Case</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleClearClick}>Clear All</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleCapClick}>Capitalize Text</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleInClick}>Inverse Text</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleCopyClick}>Copy Text</button>
<button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={handleExtraSp}>Remove Extra Space</button>
</div>
<div className="container my-3 " >
  <h3 style={{color: props.mode==='dark'?'white':'black'}}>Your Text Summary</h3>
  <p style={{color: props.mode==='dark'?'white':'black'}}>{text.split(/\s+/).filter(function(element){return element.length!==0}).length} words and {text.length} characters</p>
  <p style={{color: props.mode==='dark'?'white':'black'}}>{0.008 * text.split(/\s+/).filter(function(element){return element.length1===0}).length} Minutes will be taken to read</p>
  <h4 style={{color: props.mode==='dark'?'white':'black'}}>Preview</h4>
  <p style={{color: props.mode==='dark'?'white':'black'}}>{text.length>0?text:"Enter something to preview it here"}</p>
</div>

</>
  )
}
