import './App.css';
import About from './Components/About';
import Navbar from './Components/Navbar';
import TextForm from './Components/TextForm';
import React, { useState } from 'react';
import Alert from './Components/Alert';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';


function App() {
    const [mode,setMode] = useState('light');
    const [alert, setAlert] = useState(null);
    const showAlert = function(message,type){
        setAlert({
            message: message,
            type : type
        })
        setTimeout(() => {
            setAlert(null);
        }, 1500);
    }
    const toggleMode = function(){
        if(mode==='light'){
            setMode('dark');
            document.body.style.backgroundColor = '#001f2b';
            showAlert("Dark Mode Enabled" , "success");
        }
        else{
            setMode('light');
            document.body.style.backgroundColor = 'white';
            showAlert("Light Mode Enabled" , "success");
        }
    }
     return ( 
        <>
     <Router>
        <Navbar title = "TextUtils" mode={mode} toggleMode = {toggleMode} aboutText = "About Us" />
        <Alert alert={alert}/>
        <div className="container">
        <Routes>
          <Route exact path="/about" element={<About mode={mode}/>}/>
          <Route exact path="/" element={<TextForm showAlert={showAlert} heading="Text Utils" mode={mode}/>}/>
          </Routes>
        </div>
        </Router>
        </>  
    );
}

export default App;